.searchFromInput {
    font-size: 20px;
    width:600px;
    height:30px;
}

.fromBlock{
    display:flex;
    justify-content:center;
}

.searchBtn{
    margin-top: 1px;
    margin-left: 3px;
    width: 35px;
    height:34px;
    border: 0;
}

.searchBtn:hover{
    cursor: pointer;
    background-color:rgb(119, 119, 119);
}

.errorsText{
    margin-top:10px;
    font-size: 15px;
    color:white;
}
.printTypeBlockFlex{
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin: 0 auto;
    color: white;
}

.printTypeBlock{
    margin: 10px;
    width:90px;
}

.orderByBlock{
    margin: 10px;
    width:140px;
}

@media(max-width:900px){
    .searchFromInput {
        font-size: 13px;
        width:300px;
        height:20px;
    }

   .fromBlock{
        display:block;
       
    }
    
    .searchBtn{
        margin-top: 10px;
        margin-left: 0;
        width: 50px;
        height:23px;
        border: 0;
    }
    .errorsText{
        margin-top:5px;
        font-size: 12px;
    }
  }

@media(max-width:400px){
    .searchFromInput {
        font-size: 13px;
        width:300px;
        height:20px;
    }

   .fromBlock{
        display:block;
       
    }
    
    .searchBtn{
        margin-top: 10px;
        margin-bottom: 20px;
        margin-left: 0;
        width: 50px;
        height:23px;
        border: 0;
    }
    .errorsText{
        margin-top:5px;
        font-size: 12px;
    }
    .printTypeBlockFlex{
        display: block;
        font-size:15px;
    }
    .printTypeBlock{
        margin: 0 auto;
        margin-bottom: 10px;
    }
    .orderByBlock{
        margin: 0 auto;
    }
  }
  
  @media(max-width:300px){
    .searchFromInput {
        font-size: 13px;
        width:200px;
        height:20px;
    }

   .fromBlock{
        display:block;
       
    }
    
    .searchBtn{
        margin-top: 10px;
        margin-left: 0;
        width: 50px;
        height:23px;
        border: 0;
    }
    .errorsText{
        margin-top:5px;
        font-size: 12px;
    }
  }