.pageBooks{
    margin-top: 50px;
}

.searchResultPage {
    text-align: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 1em;
}

.bookItem{
    text-align: center;
    float: left;
    width: 25%;
}

.bookResult p,h4,h5{
    margin: 0;
    padding: 3px;
}

.bookResult{
    height:450px;
    text-align: center;
    padding: 30px 50px 0 50px
}

.bookResult img{
    border: solid 1px black;
    box-shadow: 0 0 20px black;
    width:150px;
    height: 200px;
}

.bookResult img:hover{
    width:155px;
    height: 205px;
}

.infoBook{
    padding-top: 10px;
}

.PagesBloc{
    text-align: center;
    display:flex;
    justify-content: center;
}

.PagesBloc button{
    width:50px;
    background-color:white;
    border:none;
}


.buttonAktive{
font-weight: 800;
}

.PagesBloc button:hover{
    cursor: pointer;
}
.chevronBoubleLeft img:hover{
    cursor: pointer;
}
.chevronBoubleRight img:hover{
    cursor: pointer;
}

@media(max-width:900px){
    .searchResultPage {
        text-align: center;
        display: block;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 10px;
        grid-row-gap: 1em;
    }

    .bookResult{
        height:450px;
        text-align: center;
        padding: 10px 50px 0 50px
    }
}

@media(max-width:450px){
    .searchResultPage {
        text-align: center;
        display: block;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 10px;
        grid-row-gap: 1em;
    }

    .bookResult{
        height:100%;
        text-align: center;
        padding: 40px 10px 0 10px
    }

    .PagesBloc button{
        width:30px;
        background-color:white;
        border:none;
    }
    
    
    .buttonAktive{
    font-weight: 800;
    }
}

@media(max-width:340px){
    .searchResultPage {
        text-align: center;
        display: block;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 10px;
        grid-row-gap: 1em;
    }

    .bookResult{
        height:100%;
        text-align: center;
        padding: 40px 10px 0 10px
    }

    .PagesBloc button{
        width:30px;
        background-color:white;
        border:none;
    }
    
    
    .buttonAktive{
    font-weight: 800;
    }
}


